import React, { useEffect, useState } from 'react'
import "./portfolio.css"
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import { Header } from './header/Header'
import { Home } from './home/Home'
import { Footer } from './footer/Footer'
import { Projects } from './projects/projects'
import { Contact } from './contact/Contact'

export const Portfolio = () => {


  const [scrollPercentage, setScrollPercentage] = useState(0);

  const updateScrollPercentage = () => {
    const scrollTop = window.scrollY;
    const scrollHeight = document.documentElement.scrollHeight - window.innerHeight;
    const percentage = (scrollTop / scrollHeight) * 100;
    setScrollPercentage(percentage);
  };

  useEffect(() => {
    window.addEventListener("scroll", updateScrollPercentage);
    window.addEventListener("resize", updateScrollPercentage);
    return () => {
      window.removeEventListener("scroll", updateScrollPercentage);
      window.removeEventListener("resize", updateScrollPercentage);
    };
  }, []);
  

  return (
    
    <>
    {/* extra things */}
    {/* floating stone */}
    <div className="stone stone1"></div>
    <div className="stone stone2"></div>

    <div className='scroll_width_con' style={{ borderRadius: "150px", zIndex:2 , position: "fixed", left: 0, width: "100%", height: "5px", background: "" }}>
      <div className='scroll_width' style={{ width: `${scrollPercentage}%`, height: "5px"}}></div>
    </div>


    <BrowserRouter>
        {/* header */}
        <Header/>

      <Routes>
        {/* home page */}
        <Route path='/' element={<Home/>} />
        
        {/* projects page */}
        <Route path='/projects' element={<Projects/>} />
        
        {/* contact page */}
        <Route path='/contact' element={<Contact/>} />

      </Routes>

      <Footer/>

    </BrowserRouter>


        

    </>
  )
}
